.dialog {
  position: fixed;
  display: flex;
   align-items: center;
  justify-content: center;
  top: 0;
  left: 0;
  right: 0;
  bottom:0;
  .dialog_wrap{
    z-index: 20;
    top: 200px;
    left: 50%;
    width: 90%;
    border: 2px solid #000;
    outline: none;
    padding: 10px;
    max-width: 650px;
    background: #fff;
    box-shadow: 0px 3px 5px -1px rgba(0,0,0,0.2), 0px 5px 8px 0px rgba(0,0,0,0.14), 0px 1px 14px 0px rgba(0,0,0,0.12);
    border-radius: 5px;
  }
}