.blog_main {
  display: flex;
  padding: 40px;
  .subscription_container {
    flex-basis: 33%;
    .position_title {
      margin: 0 0 0.6em 1em;
    }
    .subscription_content {
      margin: 0 0 0 1em;
      .subscription_item {
        margin: 0 0 0 1em;
        .company_content {
          display: flex;
          align-items: center;
          img {
            margin: 0 0.6em 0 0;
            width: 1.2em;
            height: 1.2em;
          }
          span {
            color: grey;
          }
        }
        .item_content {
          margin: 0 0 0 2.6em;
          div {
            a {
              font-size: 0.58em;
              color: rgb(102, 102, 102);
              margin: 0.8em 0px;
              display: block;
            }
          }
        }
      }
    }
  }
  .blog_container {
    flex-basis: 43%;
    h2 {
      font-weight: bold;
    }
    .blog_item {
      .title {
        font-size: 18px;
        color: #000;
      }
      .site {
        span {
          font-size: 13px;
          color: gray;
          &:nth-child(2) {
            margin-left: 10px;
          }
        }
      }
      .description {
        color: gray;
        font-size: 13px;
      }
    }
  }
}
