.loading_wrap{
  position: absolute;
  z-index:19;
  background: rgba(255, 255, 255, 0.9);
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 200px;
  i{
    z-index: 20;
    font-size: 4em;
    animation: 1s infinite linear Myrotate;
  }
}

@keyframes Myrotate {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}