.res_box {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  position: relative;
  .h3_title {
    flex-basis: 100%;
    &:nth-of-type(n+2){
      padding-top: 3em;
    }
  }
  .res_item {
    flex-grow: 0;
    flex-basis: calc(50% - 6px);
    max-width: 50%;
    margin: 2px;
    box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
    border-radius: 4px;
    border: 1px #eee solid;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    cursor: pointer;
    
    .res_wrap {
      padding: 16px;

      .res_header {
        display: flex;
        padding-bottom: 16px;
        align-items: center;

        .res_avatar {
          padding: 4px;
          margin-right: 12px;

          img {
            width: 35px;
            height: 35px;
          }
        }

        .title_wrap {
          font-size: 0.875rem;
          font-family: "Roboto", "Helvetica", "Arial", sans-serif;
          font-weight: 400;
          line-height: 1.43;
          letter-spacing: 0.01071em;

          .title {

            color: #000;
            font-size: 18px;
            font-weight: bold;
            cursor: pointer;

            &:hover {
              color: #0366d6;
            }
          }

          .description {
            color: #777;
            cursor: pointer;

            &:hover {
              color: #0366d6;
            }
          }
        }
      }

      .res_content {
        overflow: hidden;
        margin: 0;
        font-size: 90%;
        color: #868e96;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 9;
       -webkit-box-orient: vertical;
      }
    }

    .res_footer {
      display: flex;
      padding: 8px 15px;
      align-items: center;
      font-size: 0.75rem;
      font-family: "Roboto", "Helvetica", "Arial", sans-serif;
      font-weight: 400;
      color: rgba(0, 0, 0, 0.54);
      line-height: 1.66;
      letter-spacing: 0.03333em;

      .point {
        display: inline-block;
        width: 12px;
        height: 12px;
        border-radius: 50%;
        background: gray;
        margin-right: 5px;
      }

      .span {
        margin-right: 3px;

        &:nth-child(n+2) {
          margin-left: 10px;

          i {
            margin-right: 3px;
          }
        }
      }

    }

    .res_tagList{
      display: flex;
      flex-wrap: wrap;
      padding-top: 13px;
    }
    
  }

}