.markdown-body {
  font-size: 16px;
  blockquote {
    padding: 0 1em;
    color: #6a737d;
    border-left: 0.25em solid #dfe2e5;
  }
  a {
    background-color: initial;
    color: #0366d6;
    text-decoration: none;
  }
  ol,
  ul {
    padding-left: 2em;
  }
  ul {
    margin-top: 0;
    margin-bottom: 16px;
  }
  li {
    line-height: 1.5;
  }
}
