.basicLayout {
  header {
    height: 4em;
    width: 100%;
    position: fixed;
    top: 0;
    background: #fff;
    box-shadow: 0 3px 3px rgba(0, 0, 0, 0.3);
    background: #24292e;
    color: rgba(0, 0, 1, 0.7);
    display: flex;

    .logo_wrap {
      flex-grow: 0;
      height: 100%;
      padding: 0 10px;
      display: flex;
      align-items: center;
      font-size: 16px;
      font-weight: bold;
      color: #fff;
      cursor: pointer;

      .menu {
        display: none;
      }

      img {
        margin: 0 10px;
        width: 25px;
        padding: 3px;
      }

    }

    .searchContent {
      display: flex;
      flex-grow: 1;
      align-items: center;
      // justify-content: center;

      .search_wrap {
        position: relative;
        width: 54%;
        margin-left: 20%;

        input {
          font: inherit;
          color: #fff;
          border-radius: 11px;
          background: rgba(255, 255, 255, 0.15);
          border: 0;
          height: 2.876em;
          display: block;
          padding: 6px 0 7px 47px;
          width: 100%;
          outline: none;
        }

        img {
          position: absolute;
          width: 1.8em;
          left: 0.4em;
          top: 0.5em;
        }
      }

    }
  }

  footer {
    line-height: 1.78em;
    height: 1.78em;
    width: 100%;
    position: fixed;
    bottom: 0;
    background: #fff;
    box-shadow: 0 -3px 3px rgba(0, 0, 0, 0.3);
    text-align: center;
    z-index: 20
  }

  main {
    display: flex;
    height: calc(100vh - 5.78em);
    margin-top: 4em;

    nav {
      width: 8.2em;
      height: 100%;
      border-right: 1px solid rgba(0, 0, 0, 0.12);
      overflow: auto;
      .menu_list {

        height: 4.7em;
        display: flex;
        align-items: center;
        justify-content: center;

        a {
          text-decoration: none;
          color: #aaa;
          &.active{
            color: #00a1d6;
          }
        }
        
        .menu_content {
          height: 5.2em;
          width: 5.3em;
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          cursor: pointer;
          font-size: 13px;
          i{
            font-size:25px
          }
          img {
            width: 28px;
          }

          &:hover {
            background: rgba(0, 0, 0, 0.04)
          }

          span {
            font-size: 12px;
            margin-top: 0.6em;
          }
        }
      }

    }

    .show_content {
      flex: 1;
      height: 100%;
      overflow: auto;

      .content_wrap {
        flex-grow: 0;
        max-width: 58.333333%;
        flex-basis: 58.333333%;
        margin: 0 auto;
        padding: 20px;
      }
    }
  }

  @media screen and (max-width: 960px) {
    header {
      .logo_wrap {
        .menu {
          display: block;
        }
      }
    }

    main {
      nav {
        position: fixed;
        top: 0;
        left: 0;
        bottom: 0;
        transform: translateX(-100%);
        background: #fff;
        z-index: 19;

        &.expand {
          transform: translateX(0);
          transition: all 0.3s;
        }
      }

      .show_content {
        .content_wrap {
          max-width: 100%;
          flex-basis: 100%;

          // .res_box {
          //   .res_item {
          //     flex-basis: 98%;
          //     max-width: 98%;
          //   }
          // }
        }
      }
    }
  }

  @media screen and (max-width: 600px) {
    header {
      .logo_wrap {
        span {
          display: none;
        }
      }
    }

    main {
      .show_content {
        .content_wrap {
          .res_box {
            .res_item {
              flex-basis: 98%;
              max-width: 98%;
            }
          }
        }
      }
    }
  }
}